.all {
  display: flex;
}

.side {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding-top: 10rem;
  padding-left: 10rem;
  padding-right: 10rem;
  padding-bottom: 3rem;
}

.side img {
  height: 170px;
  border-radius: 50%;
  border: solid 1px #c2c2c2;
  margin-bottom: 1rem;
}

.side h1 {
  font-family: 'BIZ UDGothic', sans-serif;
  margin-bottom: 0.5rem;
  width: 200px;
}

.side .divleg p {
  font-family: 'Anek Malayalam', sans-serif;
  color: #000000;
  width: 250px;
  margin-bottom: 2rem;
}

.git {
  display: flex;
  margin-bottom: 0.5rem;
  align-items: center;
}

.git p {
  font-family: 'Anek Malayalam', sans-serif;
  margin-left: 0.5rem;
  font-weight: bolder;
  margin-top: 1px;
}

.linked {
  display: flex;
  margin-bottom: 0.5rem;
  align-items: center;
}

.linked p {
  font-family: 'Anek Malayalam', sans-serif;
  margin-left: 0.5rem;
  font-weight: bolder;
  margin-top: 1px;
}

.email {
  display: flex;
  margin-bottom: 0.5rem;
  align-items: center;
}

.email p {
  font-family: 'Anek Malayalam', sans-serif;
  margin-left: 0.5rem;
  font-weight: bolder;
}

.phone {
  display: flex;
  align-items: center;
  width: 200px;
  justify-content: center;
}

.phone p {
  font-family: 'Anek Malayalam', sans-serif;
  margin-left: 0.5rem;
  font-weight: bolder;
}

.side a {
  text-decoration: none;
  color: #000000;
}

.rest {
  padding: 2rem;
  padding-top: 6rem;
}

.briefing {
  max-width: 800px;
}

.briefing h1 {
  font-family: 'BIZ UDGothic', sans-serif;
  margin-bottom: 2rem;
}

.briefing p {
  font-family: 'Anek Malayalam', sans-serif;
  color: #000000;
  margin-bottom: 2rem;
}

.skillst {
  max-width: 800px;
}

.skills {
  display: flex;
  margin-bottom: 2rem;
}

.skillst h1 {
  font-family: 'BIZ UDGothic', sans-serif;
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.skills li {
  font-family: 'Anek Malayalam', sans-serif;
}

.lista1 {
  padding-left: 1.5rem;
  width: 30%;
}

.edc {
  max-width: 800px;
}

.edc h1 {
  font-family: 'BIZ UDGothic', sans-serif;
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.edc h2 {
  font-family: 'Anek Malayalam', sans-serif;
  font-weight: 500;
}

.edc h3 {
  font-family: 'Anek Malayalam', sans-serif;
  font-weight: 400;
  font-size: medium;
  margin-bottom: 2rem;
}

.projects {
  max-width: 800px;
}

.projects h1 {
  font-family: 'BIZ UDGothic', sans-serif;
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.wtdiv {
  display: flex;
  max-width: 800px;
  margin-bottom: 2rem;
  align-items: center;
}

.wtd {
  max-width: 165px;
  max-height: 165px;
  border: solid 1px #b3b3b3;
}

.textwtd {
  padding-left: 10rem;
}
.linkwtd {
  color: #000000;
}

.wtda {
  margin-top: 1rem;
}

.wtda .p1 {
  font-family: 'Anek Malayalam', sans-serif;
  font-weight: bold;
  font-size: medium;
  color: #000000;
}

.wtda .p2 {
  font-family: 'Anek Malayalam', sans-serif;
  font-weight: bold;
  font-size: medium;
  color: #000000;
  display: none;
}

.projects h2 {
  font-family: 'Anek Malayalam', sans-serif;
  font-weight: 500;
}

.projects .pp {
  font-family: 'Anek Malayalam', sans-serif;
  font-weight: 400;
  font-size: medium;
  margin-top: 0.7rem;
}

.experi {
  max-width: 800px;
}

.experi h1 {
  font-family: 'BIZ UDGothic', sans-serif;
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.experi h2 {
  font-family: 'Anek Malayalam', sans-serif;
  font-weight: 500;
}

.experi h3 {
  font-family: 'Anek Malayalam', sans-serif;
  font-weight: 400;
  font-size: medium;
}

.experi ul {
  margin-bottom: 1rem;
  list-style: none;
}
.experi li {
  font-family: 'Anek Malayalam', sans-serif;
  font-size: 14px;
}

.experi .thi {
  margin-bottom: 2rem;
}

@media only screen and (max-width: 1200px) {
  .all {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .rest {
    display: flex;
    flex-direction: column;
    padding: 2rem;
    max-width: 800px;
  }

  .lista1 {
    width: 60%;
  }

  .textwtd {
    padding-left: 1.5rem;
  }
  .wtda .p1 {
    display: none;
  }

  .wtda .p2 {
    display: flex;
  }

  .side {
    padding-top: 3rem;
  }
}
